import { html, LitElement } from "lit";
import { ref, createRef } from "lit/directives/ref.js";
import { classMap } from "lit/directives/class-map.js";
import { PxApi } from "@sap-px/pxapi";
import { setCustomElementsScopingSuffix } from "@ui5/webcomponents-base/dist/CustomElementsScope.js";
setCustomElementsScopingSuffix("cx-qtx-btn");
import "@ui5/webcomponents/dist/Assets.js";
import "@ui5/webcomponents-fiori/dist/Assets.js";
import "@ui5/webcomponents-fiori/dist/illustrations/Survey.js";
import "@ui5/webcomponents-icons/dist/feedback.js";
import {
    registerI18nLoader,
    getI18nBundle
} from "@ui5/webcomponents-base/dist/i18nBundle.js";
import { setFetchDefaultLanguage } from "@ui5/webcomponents-base/dist/config/Language.js";
import { setTheme } from "@ui5/webcomponents-base/dist/config/Theme.js";
import getThemeDesignerTheme from "@ui5/webcomponents-base/dist/theming/getThemeDesignerTheme.js";
import legacyFeedbackIconSvg from "./assets/legacyFeedbackIcon.svg.js";
import newFeedbackIconSvg from "./assets/newFeedbackIcon.svg.js";
import css from "./CxQtxSurveyButton.css.js";

/* eslint-disable-next-line import/prefer-default-export */
export class CxQtxSurveyButton extends LitElement {
    constructor() {
        super();

        // We need to set the theme **before** importing the UI5 Web Component
        // in order to prevent them from overwriting the global theme variables with the
        // UI5 default theme (sap_fiori_3). See issue:
        // https://github.tools.sap/CX-UX-Engineering/cx-qtx-survey-button/issues/27
        this.theme = getThemeDesignerTheme()?.themeName || "sap_horizon";
        setTheme(this.theme);

        import("@ui5/webcomponents/dist/Button.js");
        import("@ui5/webcomponents/dist/Dialog.js");
        import("@ui5/webcomponents/dist/Icon.js");
        import("@ui5/webcomponents-fiori/dist/Bar.js");
        import("@ui5/webcomponents-fiori/dist/IllustratedMessage.js");

        this._version = __VERSION__;
        this.timePushDisabled = false;
        this.appPushConfigs = [];

        this.surveyNotificationPromise = {
            resolve() { },
            reject() { }
        };

        this.contextParams = {};

        this.allowedOrigins = [window.location.origin];

        // Required properties
        this.interceptUrl = "";
        this.tenantId = "";
        this.tenantRole = "";

        this.productName = "";
        this.platformType = "";

        this.unitId = "";
        this.environment = "";

        this._pxApi = new PxApi();
        this._pxApiInitialized = false;
        this._dialogRef = createRef();
        this._showNotificationBadge = this._getLocalStorageSetting("showNotificationBadge") || false;

        this._addPostMessageEventHandler();
        this._registerI18nLoader();
    }

    static shadowRootOptions = {
        ...LitElement.shadowRootOptions,
        delegatesFocus: true
    };

    static get properties() {
        return {
            timePushDisabled: {
                type: Boolean,
                attribute: "time-push-disabled"
            },
            timePushSuspended: {
                type: Boolean,
                attribute: "time-push-suspended"
            },
            appPushConfigs: {
                type: Array,
                attribute: "app-push-configs"
            },
            interceptUrl: {
                type: String,
                attribute: "intercept-url"
            },
            contextParams: {
                type: Object,
                attribute: "context-params",
                reflect: true
            },
            theme: {
                type: String,
                attribute: "theme"
            },
            allowedOrigins: {
                type: Array,
                attribute: "allowed-origins"
            },
            tenantId: {
                type: String,
                attribute: "tenant-id"
            },
            tenantRole: {
                type: String,
                attribute: "tenant-role"
            },
            productName: {
                type: String,
                attribute: "product-name"
            },
            platformType: {
                type: String,
                attribute: "platform-type"
            },
            unitId: {
                type: String,
                attribute: "unit-id"
            },
            environment: {
                type: String
            },
            _showNotificationBadge: {
                type: Boolean,
                state: true
            }
        };
    }

    _validateProps() {
        // For mandatory configuration parameters see: https://pages.github.tools.sap/sap-px/sap-px/README_pxconfig.html#mandatory-configuration-properties
        const requiredProps = [
            {
                property: "interceptUrl",
                attribute: CxQtxSurveyButton.properties.interceptUrl.attribute,
                value: this.interceptUrl
            },
            {
                property: "tenantId",
                attribute: CxQtxSurveyButton.properties.tenantId.attribute,
                value: this.tenantId
            },
            {
                property: "tenantRole",
                attribute: CxQtxSurveyButton.properties.tenantRole.attribute,
                value: this.tenantRole
            },
            {
                property: "productName",
                attribute: CxQtxSurveyButton.properties.productName.attribute,
                value: this.productName
            },
            {
                property: "platformType",
                attribute: CxQtxSurveyButton.properties.platformType.attribute,
                value: this.platformType
            },
            {
                property: "unitId",
                attribute: CxQtxSurveyButton.properties.unitId.attribute,
                value: this.unitId
            },
            {
                property: "environment",
                attribute: CxQtxSurveyButton.properties.environment.attribute,
                value: this.environment
            }
        ];

        return (
            requiredProps
                .map((prop) => {
                    if (prop.value === "") {
                        /* eslint-disable-next-line no-console */
                        console.error(
                            `CxQtxSurveyButton: Required property '${prop.property}' or attribute '${prop.attribute}' missing.`
                        );
                        return false;
                    }

                    return true;
                })
                .filter((val) => val).length === requiredProps.length
        );
    }

    _getLocalStorageSetting(key) {
        try {
            const settings = JSON.parse(localStorage.getItem("sap.cx.qtx.survey.button"));
            return settings[key];
        } catch (error) {
            localStorage.setItem("sap.cx.qtx.survey.button", "{}");
            return;
        }
    }

    _setLocalStorageSetting(key, value) {
        try {
            const settings = JSON.parse(localStorage.getItem("sap.cx.qtx.survey.button"));
            settings[key] = value;
            localStorage.setItem("sap.cx.qtx.survey.button", JSON.stringify(settings));
        } catch (error) {
            localStorage.setItem("sap.cx.qtx.survey.button", JSON.stringify({
                [key]: value
            }));
        }
    }

    _addPostMessageEventHandler() {
        window.addEventListener("message", (event) => {
            if (event.data?.receiver !== "cx-qtx-survey-button") return;
            if (!this.allowedOrigins.includes(event.origin)) {
                // eslint-disable-next-line no-console
                return console.error(
                    `CxQtxSurveyButton: Ignoring postMessage. Source origin '${event.origin}' is not part of the allowed origins: '${this.allowedOrigins}'.`
                );
            }
            this[event.data?.method](...event.data?.params);
        });
    }

    _registerI18nLoader() {
        setFetchDefaultLanguage(true);
        const supportedLocales = [
            "ar",
            "bg",
            "ca",
            "cs",
            "cy",
            "da",
            "de",
            "el",
            "en",
            "en_GB",
            "en_US_saprigi",
            "es",
            "es_MX",
            "et",
            "fi",
            "fr",
            "fr_CA",
            "hi",
            "hr",
            "hu",
            // "in", missing
            "id", // new
            "it",
            "iw",
            "ja",
            "kk",
            "ko",
            "lt",
            "lv",
            "ms",
            "nl",
            "no",
            "pl",
            "pt",
            "pt_PT",
            "ro",
            "ru",
            "sh",
            "sk",
            "sl",
            "sv",
            "th",
            "tr",
            "uk",
            "vi",
            "zh_CN",
            "zh_TW"
        ];
        supportedLocales.forEach((localeToRegister) => {
            registerI18nLoader(
                "cx-qtx-survey-button",
                localeToRegister,
                async (localeId) => {
                    return (await import(`../i18n/i18n_${localeId}.json`)).default;
                }
            );
        });
    }

    async _onClick() {
        if (!this._pxApiInitialized) return;
        await this._pxApi.openSurvey(this.contextParams);
        this._setLocalStorageSetting("showNotificationBadge", false);
        this._showNotificationBadge = false;
    }

    _resolveSurveyNotification(state) {
        this._setLocalStorageSetting("showNotificationBadge", !state);
        this._showNotificationBadge = !state;
        this.surveyNotificationPromise.resolve({
            appContextData: this.contextParams,
            surveyUser: state
        });
    }

    async _onSurveyNotification() {
        return new Promise((resolve, reject) => {
            // Reject promise to avoid stale promise chains from previous notifications.
            this.surveyNotificationPromise.reject();
            this.surveyNotificationPromise = {
                resolve,
                reject
            };

            // We need to move the dialog out of the shadow dom in order to avoid z-index issues.
            // If the button is placed inside a shellbar, which has a z-index lower than 101, the dialog
            // inherits this z-index and will be placed below the ui5-block-layer of the ui5-static-area.
            // By moving the dialog out of the shadow root into the body we prevent this issue.
            document.body.appendChild(this._dialogRef.value);
            this._dialogRef.value?.show();
        }).finally(() => {
            this._dialogRef.value?.close();
        });
    }

    _handleDialogClose({ detail }) {
        if (detail.escPressed) {
            // If the dialog was closed by pressing ESC we need to resolve the PX-API promise with false.
            this._resolveSurveyNotification(false);
        }
    }

    /*
     * Update all parameters
     */
    setContextParams(newParams) {
        // Keep the parameters stored, and lazily set them on the next click
        this.contextParams = JSON.parse(JSON.stringify(newParams));
    }

    /*
     * Update a single parameter
     */
    updateContextParam(paramName, paramValue) {
        this.contextParams[paramName] = paramValue;
    }

    /*
     * Call to request an 'App Push' (see: https://pages.github.tools.sap/sap-px/sap-px-api-docs/2.5.0/features/pushFeedback/appTriggeredPush/).
     * Requires a valid configuration by setting the appPushConfigs property.
     */
    requestSurveyPush(pushFeedbackRequestData) {
        if (!this._pxApiInitialized) return;
        this._pxApi.requestPush(pushFeedbackRequestData);
    }

    async _initializePXApi() {
        if (this._pxApiInitialized) {
            this._pxApi.destroy();
        }

        const scopeSet = [
            "manual",
            ...(!this.timePushDisabled ? ["timedPush"] : []),
            ...(this.appPushConfigs.length > 0 ? ["appPush"] : [])
        ];

        const pxApiConfig = {
            version: "0.3.1",
            unitId: this.unitId,
            environment: this.environment,
            startupConfig: {
                qualtricsInternalUri: this.interceptUrl,
                enableQualtricsRestrictedMode: true,
                productName: this.productName,
                platformType: this.platformType,
                scopeSet
            },
            themingConfig: {
                writeToGlobals: true
            },
            pushConfig: {
                appPush: {
                    configs: this.appPushConfigs
                }
            }
        };

        await this._pxApi.initialize(
            {
                tenantId: this.tenantId,
                tenantRole: this.tenantRole
            },
            null,
            pxApiConfig,
            this._onSurveyNotification.bind(this)
        );
        this._pxApi.currentThemeId = this.theme;
        this._pxApiInitialized = true;
    }

    connectedCallback() {
        // We need to wait for the event loop to be clear in order to make sure that
        // all dynamic attributes are set by the subsequent frameworks like Vue, React, Angular, etc.
        setTimeout(async () => {
            if (!this._validateProps()) return;

            this.i18n = await getI18nBundle("cx-qtx-survey-button");

            super.connectedCallback();

            this._initializePXApi();
            this.setAttribute("aria-hidden", "true");
        });
    }

    updated(changedProperties) {
        if (changedProperties.has("theme")) {
            setTheme(this.theme);
            this._pxApi.currentThemeId = this.theme;
        }
        if (changedProperties.has("timePushSuspended")) {
            sap = sap ?? {};
            sap.pxdata = sap.pxdata ?? {};
            sap.pxdata.settings = sap.pxdata.settings ?? {};
            sap.pxdata.settings.standalone = !!this.timePushSuspended;
        }
    }

    async attributeChangedCallback(name, oldValue, newValue) {
        super.attributeChangedCallback(name, oldValue, newValue);

        // Once the PX API is initialized, the configuration cannot be changed on demand.
        // To adapt the configuration we need to destroy and re-create the PX API instance.
        // We need to do this every time an attribute value is changed which is used inside
        // the configuration.
        const pxApiConfigAttrNames = [
            CxQtxSurveyButton.properties.tenantId.attribute,
            CxQtxSurveyButton.properties.tenantRole.attribute,
            CxQtxSurveyButton.properties.interceptUrl.attribute,
            CxQtxSurveyButton.properties.productName.attribute,
            CxQtxSurveyButton.properties.platformType.attribute,
            CxQtxSurveyButton.properties.unitId.attribute,
            CxQtxSurveyButton.properties.environment.attribute,
            CxQtxSurveyButton.properties.timePushDisabled.attribute
        ];

        if (
            oldValue !== newValue &&
            pxApiConfigAttrNames.includes(name) &&
            this._pxApiInitialized
        ) {
            await this._initializePXApi();
        }
    }

    disconnectedCallback() {
        this._dialogRef.value?.remove();
        this._pxApi.destroy();

        super.disconnectedCallback();
    }

    render() {
        const svgIcon = /sap_horizon.*/.test(this.theme) ? newFeedbackIconSvg : legacyFeedbackIconSvg;

        return html`
            <div @click="${this._onClick}" class=${classMap({ notificationBadge: this._showNotificationBadge })}>
                <slot>
                    <ui5-button-cx-qtx-btn
                        design="Transparent"
                        tooltip="${this.i18n.getText(
            "SHELLBAR_BUTTON_TOOLTIP"
        )}"
                        accessible-name="${this.i18n.getText(
            "SHELLBAR_BUTTON_TOOLTIP"
        )}"
                    >
                        ${svgIcon}
                    </ui5-button-cx-qtx-btn>
                </slot>
            </div>
            <ui5-dialog-cx-qtx-btn
                id="cx-qtx-survey-button-dialog"
                initial-focus="provide-feedback-button"
                ${ref(this._dialogRef)}
                @before-close="${this._handleDialogClose}"
            >
                <style>
                    ::part(footer),
                    ::part(header) {
                        padding-inline: 0;
                    }
                </style>
                <ui5-bar-cx-qtx-btn slot="header">
                    <ui5-icon-cx-qtx-btn
                        slot="startContent"
                        design="Default"
                        name="feedback"
                    ></ui5-icon-cx-qtx-btn>
                    <p slot="startContent">${this.i18n.getText("YOUR_OPINION_TITLE")}</p>
                </ui5-bar-cx-qtx-btn>
                <ui5-illustrated-message-cx-qtx-btn
                    name="Survey"
                    size="Dialog"
                    title-text=" "
                    subtitle-text="${this.i18n.getText("YOUR_OPINION_TEXT")}"
                ></ui5-illustrated-message-cx-qtx-btn>
                <ui5-bar-cx-qtx-btn slot="footer">
                    <ui5-button-cx-qtx-btn
                        id="provide-feedback-button"
                        slot="endContent"
                        design="Emphasized"
                        @click="${() => this._resolveSurveyNotification(true)}"
                        >${this.i18n.getText(
            "YOUR_OPINION_PROVIDEBUTTON"
        )}</ui5-button-cx-qtx-btn
                    >
                    <ui5-button-cx-qtx-btn
                        id="ask-later-button"
                        slot="endContent"
                        design="Transparent"
                        @click="${() => this._resolveSurveyNotification(false)}"
                        >${this.i18n.getText(
            "YOUR_OPINION_ASKLATERBUTTON"
        )}</ui5-button-cx-qtx-btn
                    >
                </ui5-bar-cx-qtx-btn>
            </ui5-dialog-cx-qtx-btn>
        `;
    }

    static get styles() {
        return [css];
    }
}

window.customElements.define("cx-qtx-survey-button", CxQtxSurveyButton);
